import "./GameOver.css";

const GameOver = ({ score, changeEndGame }) => {
  return (
    <div className="gameoverPage">
      <div className="gameoverPage__opaciy"></div>
      <div className="gameoverPage__result-Wrapper">
        <p>Przegrana</p>
        <p>Ilosć punktów: {score}</p>
        <button onClick={changeEndGame}>Menu główne</button>
      </div>
    </div>
  );
};

export default GameOver;
